body{
  background-color: #F6F7FB;
  color: #474D58;
  transition: background-color 1s ease;
}

a{
  text-decoration: none;
}
a:hover { 
  text-decoration:none; 
}

Link{
  text-decoration: none;
}
Link:hover { 
  text-decoration:none; 
}

.grid {
  display: grid;
  place-items: center;
}

.userName {
  background: #3d62e0;
  background-image: linear-gradient(40deg, #6658dd, #f1556c);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.title {
  background-color: #f3ec78;
  background-image: linear-gradient(45deg, #25a5e6, #0d3ecc);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-text-fill-color: transparent;
}

.subtitle {
  background-color: #f3ec78;
  background-image: linear-gradient(45deg, #8727ff , #ff9fe1);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-text-fill-color: transparent;
}